"use strict";

var _ = require('lodash');

var cityMapping = require('./data/cityMap.json');

function lookupViaCity(city) {
  var cityLookup = _.filter(cityMapping, function (o) {
    return o.city.toLowerCase() === city.toLowerCase();
  });

  if (cityLookup && cityLookup.length) {
    return cityLookup;
  } else {
    return [];
  }
}

function findPartialMatch(itemsToSearch, searchString) {
  var searchItems = searchString.split(" ");
  var isPartialMatch = searchItems.every(function (i) {
    return itemsToSearch.join().toLowerCase().indexOf(i.toLowerCase()) >= 0;
  });
  return isPartialMatch;
}

function findFromCityStateProvince(searchString) {
  if (searchString) {
    var cityLookup = _.filter(cityMapping, function (o) {
      return findPartialMatch([o.city, o.state_ansi, o.province, o.country], searchString);
    });

    if (cityLookup && cityLookup.length) {
      return cityLookup;
    } else {
      return [];
    }
  } else {
    return [];
  }
}

module.exports = {
  lookupViaCity: lookupViaCity,
  findFromCityStateProvince: findFromCityStateProvince
};